.navBar {
    display: flex;
    background-color: #000000; /* Background color of the navigation bar */
    color: #fff; /* Text color of the navigation links */
    padding: 10px; /* Padding around the navigation links */
    height: 80px; 
    width: 100%;
}
.navContainer {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  width: 100%
}

.subjectMenu {
position: absolute;
list-style-type: none;
display: flex;
gap: 35px;
left: 20%;
}

.subjectMenu li a {
 color: rgb(255, 255, 255);
 text-decoration: none;
 font-size: 20px;
}
.logo {
    position: absolute;
    height: auto;
    width: 170px;
    left: 1%;
}
.registerMenu {
    position: absolute;
    margin-left: 85%;
    list-style-type: none;
    display: flex;
    gap: 15px;
}
.registerMenu li a {
    color: white;
    text-decoration: none;
    font-size: 20px;
}
.main-block {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    width: 100%;
    height: 90px;
    margin-top: 4%;

}
.header {
   border-radius: 10px;
   width: 1000px; 
   display: flex; 
   flex-direction: column;
   justify-content: center; 
   align-items: center; 
   box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
   padding: 15px;
}
.main-content {
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 940px;
    height: 300px;
    background-color: #f5f5f5;

}
.title-1 {
    text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
     font-weight: normal; 
     font-size: 35px; 
     font-family: 'Libre Baskerville', serif;
}
.title-2 {
    padding: 15px; 
    text-align: justify;
    font-size: 17px;
    font-family: 'Source Sans Pro', sans-serif;
}
.title-3 {
    font-size: 23px; 
    font-weight: normal;
}
.about-me {
    position: absolute;
    width: 100%;
    height: 100px;
    padding-bottom: 10px;
    text-align: justify; 
    display: flex; 
    flex-direction: column; 
    align-items: center;
    margin-top: 23%;
    bottom: 0;
}
.about-me-2{
    padding-top: 10px; 
    width: 650px;
    height: 100px;
}