/* index.css */

/* Basic CSS Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Set default font styles */
  body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    background-color: #f8f8f8;
    color: #333;
  }
  
  /* Basic styling for the root div */
  #root {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
  }
  
  /* A few utility classes */
  .container {
    width: 80%;
    margin: auto;
    overflow: hidden;
  }
  
  .text-center {
    text-align: center;
  }
  
  .mt-2 {
    margin-top: 20px;
  }
  
  .mb-2 {
    margin-bottom: 20px;
  }
  
  .btn {
    display: inline-block;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: #333;
    border: none;
    border-radius: 5px;
  }
  
  .btn:hover {
    background-color: #555;
  }
  